<template>
  <div>
    <div style="z-index: -100;min-width: 1300px;position:absolute;top:0;bottom: 0;width: 100%;
    overflow: hidden;background: #000;">
      <video loop muted="muted" oncanplaythrough="play()" poster="../assets/login-bg2.jpg"
             style="width: 100%;object-fit:cover;">
        <source src="../assets/video.mp4" type="video/mp4">
      </video>
    </div>
    <div style="float:left;margin-top: -100px;margin-left: 20px;position: relative;height: 54px;width: 500px;">
      <img style="height: 54px; position: absolute;top: 0;left: 0;" src="../assets/khlogo.png" />
      <span class="login-app-name" lang="HEAD_TITLE"
            style="margin-left:15px;font-family:'Microsoft YaHei UI Light','Microsoft YaHei';
            font-size:20px;color:white;position: absolute;left: 55px;top:19px;">MP-JAVA 系统</span>
    </div>
    <el-form :model="loginForm" :rules="fieldRules" ref="loginForm" label-position="left" label-width="0px" class="demo-ruleForm login-container">
    <span class="tool-bar">
      <!-- 主题切换 -->
      <theme-picker style="float:right;" class="theme-picker" :default="themeColor" @onThemeChange="onThemeChange"></theme-picker>
      <!-- 语言切换 -->
      <!-- <lang-selector class="lang-selector"></lang-selector>    -->
    </span>
      <h2 class="title" style="padding-left:22px;" >新车内制成本管理系统</h2>
      <el-form-item prop="account">
        <el-input type="text" v-model="loginForm.account" auto-complete="off" placeholder="账号"></el-input>
      </el-form-item>
      <el-form-item prop="password">
        <el-input type="password" v-model="loginForm.password" auto-complete="off" placeholder="密码"></el-input>
      </el-form-item>
      <!-- <el-form-item >
        <el-col :span="12">
          <el-form-item prop="captcha">
            <el-input type="test" v-model="loginForm.captcha" auto-complete="off" placeholder="验证码, 单击图片刷新"
              style="width: 100%;">
            </el-input>
          </el-form-item>
        </el-col>
        <el-col class="line" :span="1">&nbsp;</el-col>
        <el-col :span="11">
          <el-form-item>
              <img style="width: 100%;" class="pointer" :src="loginForm.src" @click="refreshCaptcha">
          </el-form-item>
        </el-col>
      </el-form-item> -->
      <!-- <el-checkbox v-model="checked" checked class="remember">记住密码</el-checkbox> -->
      <el-form-item style="width:100%;">
        <el-button type="primary" style="width:48%;" @click.native.prevent="reset">重 置</el-button>
        <el-button type="primary" style="width:48%;" @click.native.prevent="login" :loading="loading">登 录</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import Cookies from "js-cookie"
import ThemePicker from "@/components/ThemePicker"
import LangSelector from "@/components/LangSelector"
import ErrorInfo from "@/utils/other/mp/errorinfo";
import md5 from "@/utils/other/mp/mputil";
import axios from '@/http/axios';
export default {
  name: 'mpLogin',
  components:{
    ThemePicker,
    LangSelector
  },
  data() {
    return {
      loading: false,
      loginForm: {
        account: '',
        password: ''
      },
      fieldRules: {
        account: [
          { required: true, message: '请输入账号', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' }
        ]
        // ,
        // captcha: [
        //   { required: true, message: '请输入验证码', trigger: 'blur' }
        // ]
      },
      checked: true
    }
  },
  methods: {
    login() {
      this.loading = true;
     // let tmppwd = MD5(this.loginForm.password).toLowerCase();
      debugger
      let tmpuser = this.loginForm.account;
      let tmppwd = md5.encript(this.loginForm.password,tmpuser);
      let tmpData = {
        user: tmpuser,
        pwd: tmppwd.password,
        lang: "zh-CN",
        mode: 0,
        vcode: '',
        randomCode: tmppwd.randomCode
      };
      this.$api.expapi.login(tmpData).then(res => {
        if(res.result === "0" ){
          debugger
          let message = ErrorInfo.ERRORINFO[res.msg]
          this.$message.warning( message);
          return;
        }
        let tuserid = res.userid;
        let tusername = res.username;
        let tempname = res.empname;
        let ttoken = res.token;
        axios({
          hostUrl: this.global.lyBaseUrl,
          url: "/sysRedis/saveloginInfo",
          method: "post",
          data: {
            userid: tuserid,
            username: tusername,
            empname: tempname,
            token: ttoken
          }
        }).then(res => {
          Cookies.set('token', ttoken);
          sessionStorage.setItem('user', tusername); // 保存用户到本地会话
          sessionStorage.setItem('userid',tuserid);
          this.$store.commit('menuRouteLoaded', false);
          this.$router.push('/');
        }).catch(err => {
          this.$message.warning(err.message);
        })
      }).catch(err =>{
        this.$message.warning(err.message);
      }).finally(res =>{
          this.loading = false
      });

/*      let userInfo = {account:this.loginForm.account, password:this.loginForm.password}
      this.$api.login.login(userInfo).then((res) => {
          if(res.msg != null) {
            this.$message({
              message: res.msg,
              type: 'error'
            })
          } else {
            Cookies.set('token', res.data.token) // 放置token到Cookie
            sessionStorage.setItem('user', userInfo.account) // 保存用户到本地会话
            this.$store.commit('menuRouteLoaded', false) // 要求重新加载导航菜单
            this.$router.push('/')  // 登录成功，跳转到主页
          }
          this.loading = false
        }).catch((res) => {
          this.$message({
          message: res.message,
          type: 'error'
          })
        });*/
    },
    // refreshCaptcha: function(){
    //   this.loginForm.src = this.global.baseUrl + "/captcha.jpg?t=" + new Date().getTime();
    // },
    reset() {
      this.$refs.loginForm.resetFields()
    },
    // 切换主题
    onThemeChange: function(themeColor) {
      this.$store.commit('setThemeColor', themeColor)
    }
  },
  mounted() {
    // this.refreshCaptcha()
  },
  computed:{
    ...mapState({
      themeColor: state=>state.app.themeColor
    })
  }
}
</script>

<style lang="scss" scoped>
  .login-container {
    -webkit-border-radius: 5px;
    border-radius: 5px;
    -moz-border-radius: 5px;
    background-clip: padding-box;
    margin: 100px auto;
    width: 350px;
    padding: 35px 35px 15px 35px;
    background: #fff;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;
    .title {
      margin: 0px auto 30px auto;
      text-align: center;
      color: #505458;
    }
    .remember {
      margin: 0px 0px 35px 0px;
    }
  }
</style>



